import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ReadingCode } from '@app/enum/app_enum';
import { Option } from '@app/shared/models/ReadingModel';
import { FIBDragEvaluate, FIBDropDownEvaluate, MCQEvaluate, ReadingOption, Reorder, ReorderEvaluate } from '@app/shared/models/ReadingModel';

import { CommonService } from "@core/http/serviceIndex";

// extrnal library
import { SortablejsOptions } from 'ngx-sortablejs';

@Component({
  selector: 'reading-question',
  templateUrl: './reading-question.component.html',
  styleUrls: ['./reading-question.component.css']
})
export class ReadingQuestionComponent implements OnInit {
  @Input() questionData;
  @Input() isNext: boolean = false;

  @Output() answerEmit: any = new EventEmitter<any>();

  public questionCode: any;
  public isReach: boolean = false;
  public readingCode = ReadingCode;

  // R-01
  questionOptions: SortablejsOptions = {
    group: {
      name: 'group1',
      put: false,
    },
  };

  answerOptions: SortablejsOptions = {
    group: {
      name: 'group2',
      put: ['group1', 'group2'],
    },
  };

  public fibDDAnswer: Array<ReadingOption> = [];
  public dragAnswer: Array<string> = [];
  public multipleAnswer: Array<Option> = [];
  public singleAnswer: Array<Option> = [];

  public reorderList: any = [];

  public optionIndex: any;
  isLoading: boolean = false;

  constructor(private _cservice: CommonService) { }

  ngOnInit() {
  }

  generateAnswerFormat() {
    this.isReach = true;
    let ele = document.getElementById("note");
    if (ele) {
      if (ele.parentNode)
        ele.parentNode.removeChild(ele);
    }
    switch (this.questionData.questionType) {
      case "R004": {
        let ques = document.getElementById("singleQuestion");
        ques.append(
          this._cservice.stringToEl("<div id='note'>" + this.questionData.htmlquestion + "</div>")
        );
        break;
      }
      case "R005": {
        let ques = document.getElementById("singleQuestion");
        ques.append(
          this._cservice.stringToEl("<div id='note'>" + this.questionData.htmlquestion + "</div>")
        );
        break;
      }
    }
  }



  onFibDropDown(value, index, answerId) {
    let obj = new ReadingOption;
    obj.answerId = answerId;
    obj.option = value;

    this.fibDDAnswer[index] = obj;
  }

  allowDrop(ev) {
    ev.preventDefault();
  }

  onDrop03(ev, index) {
    ev.preventDefault();
    var data = ev.dataTransfer.getData("text");
    let ele = document.getElementById("dragId" + index);
    ele.innerHTML = data;
    if (this.dragAnswer[index]) {
      let oldData = this.dragAnswer[index];
      this.dragAnswer[index] = data;
      this.questionData.options.push(oldData);
    } else {
      this.dragAnswer[index] = data;
    }

    this.questionData.options.splice(this.optionIndex, 1);
  }

  drag(ev, index) {
    this.optionIndex = index;
    ev.dataTransfer.setData("text", ev.target.textContent);
  }

  getMultiple(ans) {
    const index = this.multipleAnswer.findIndex(x => x._id === ans._id);
    if (index == -1) {
      this.multipleAnswer.push(ans);
    } else {
      this.multipleAnswer.splice(index, 1)
    }
  }

  getSingle(data) {
    this.singleAnswer = []
    this.singleAnswer.push(data);
  }


  onSubmit() {
    this.isLoading = true;
    let qId = this.questionData.questionId;
    let qtype = this.questionData.questionType;
    switch (this.questionData.questionType) {
      case this.readingCode.R001: {
        let reorder = new ReorderEvaluate;
        reorder.answers = this.reorderList;
        reorder.questionId = qId;
        reorder.questionType = qtype;
        this.answerEmit.emit(reorder);
        break;
      }
      case this.readingCode.R002: {
        let dropDown = new FIBDropDownEvaluate;
        dropDown.questionId = qId;
        dropDown.answers = this.fibDDAnswer;
        dropDown.questionType = qtype;
        this.answerEmit.emit(dropDown);
        break;
      }
      case this.readingCode.R003: {
        let dropDown = new FIBDragEvaluate;
        dropDown.questionId = qId;
        dropDown.answers = this.dragAnswer;
        dropDown.questionType = qtype;

        this.answerEmit.emit(dropDown);
        break;
      }
      case this.readingCode.R004: {
        let mcq = new MCQEvaluate;
        mcq.questionId = qId;
        mcq.answers = this.singleAnswer;
        mcq.questionType = qtype;
        this.answerEmit.emit(mcq);
        break;
      }
      case this.readingCode.R005: {
        let mcq = new MCQEvaluate;
        mcq.questionId = qId;
        mcq.answers = this.multipleAnswer;
        mcq.questionType = qtype;
        this.answerEmit.emit(mcq);
        break;
      }
    }
  }
  ngOnChanges(): void {

    this.reorderList = [];
    this.optionIndex = undefined;
    this.singleAnswer = [];
    this.multipleAnswer = [];
    this.dragAnswer = [];
    this.fibDDAnswer = [];
    this.reorderList = [];
    // this.answerEmit.emit(this.reorderList);
    this.questionData = this.questionData;
    setTimeout(() => {
      this.isLoading = false;

    }, 100);
    setTimeout(() => {
      this.generateAnswerFormat();
    }, 150);

  }
}
