import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { SpeakingCode } from '@app/enum/app_enum';
import { RecoderComponent } from '@app/shared/basic-components/components/evaluate-question/recoder/recoder.component';
import { SpeakingEvaluate } from '@app/shared/models/SpeakingModel';
import { CommonService } from "@core/http/common.service";

@Component({
  selector: 'speaking-question',
  templateUrl: './speaking-question.component.html',
  styleUrls: ['./speaking-question.component.css']
})
export class SpeakingQuestionComponent implements OnInit {
  @ViewChild(RecoderComponent, { static: false }) private recoder: RecoderComponent;

  public speakingCode = SpeakingCode;

  @Input() questionData: any;

  @Input() recoderStatusInput: any;

  @Input() isNext: boolean = false;

  @Output() answerEmit: any = new EventEmitter<any>();

  @Input() isnonEvaluate: boolean = false;

  public isImgLoad: boolean = true;
  public recoderStatus: any;
  public isRecord: boolean = false;
  public audioState: any;
  public isLoading: boolean = false;
  public clickNoEvaluate: boolean = false;

  constructor() { }

  ngOnInit() {

  }

  onemitFile(data: any) {
    console.log('reach there',data)
    this.isLoading = true;
    if (data.recoderStatus == "end") {
      this.isRecord = false;
      let evaluate = new SpeakingEvaluate;
      evaluate.questionId = this.questionData.questionId;
      evaluate.file = data.file;
      evaluate.blobUrl = data.blobUrl;
      evaluate.questionType = this.questionData.questionType;

      if (this.clickNoEvaluate)
        evaluate.isNoEvaluate = this.clickNoEvaluate;

      this.answerEmit.emit(evaluate);
    }
  }

  onImgLoad() {
    this.isImgLoad = false;
    this.isRecord = true;
    setTimeout(() => {
      this.recoder.onRestart();
    }, 100);
  }

  audioStatus(e: String) {
    if (e == "ended") {
      this.isRecord = true;
      setTimeout(() => {
        this.recoder.onRestart();
      }, 100);

      this.audioState = "end";
    }
  }



  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
  }


  ngOnChanges(changes): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
    if (changes.questionData.firstChange == false) {
      if (this.questionData.questionType == 'S001')
        this.recoder.onRestart();
      else if (this.questionData.questionType == 'S002') {
        this.audioState = 'start';
      }
    }
    setTimeout(() => {
      this.isLoading = false;
    }, 100);
  }

  onSubmit() {
    console.log('here warning')
    this.recoder.onEnd();
  }

  noEvluating() {
    console.log('reach there')
    this.clickNoEvaluate = true;
    this.recoder.isNoEvaluate=true;
    this.recoder.onEnd();
  }
}
